@import url(http://fonts.cdnfonts.com/css/neue-haas-grotesk-display-pro);
@import url(http://fonts.cdnfonts.com/css/neue-haas-grotesk-display-pro);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  background-color: black;
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 100%;
  height: 100%;
}
.ccc{
  font-size: 3em;
  background-color: white;
  color: black;
}
/* width */
::-webkit-scrollbar {
  width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #f1f1f1; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555; 
}

#container{
  display: flex;
  flex-direction: column;
  position: absolute;
  width: 100%;
  height:100%;
}

@keyframes fadeInUp{
  from{
    -webkit-transform: translate3d(0,40px,0);
            transform: translate3d(0,40px,0);
  }

  to{
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
  }
}

@-webkit-keyframes fadeInUp{
  from{
    -webkit-transform: translate3d(0,40px,0);
            transform: translate3d(0,40px,0);
  }
  to{
    -webkit-transform: translate3d(0,0,0);
            transform: translate3d(0,0,0);
    opacity: 1;
  }
}

.animated{
  animation-duration: 0.7s;
  animation-fill-mode: both;
  -webkit-animation-duration: 0.7s;
  -webkit-animation-fill-mode: both;
}

.animatedDelay{
  animation-duration: 2s;
  animation-fill-mode: both;
  -webkit-animation-duration: 2s;
  -webkit-animation-fill-mode: both;
}

.animatedFadeInUp{
  opacity: 0;
}

.fadeInUp{
  opacity: 0;
  animation-name:fadeInUp;
  -webkit-animation-name:fadeInUp;
}


.nav{
    display: flex;
    position: fixed;
    font-family: 'Neue Haas Grotesk Display Pro', sans-serif;
    z-index: 2;
    
}

.nav-info{  
    color:white;
}

.place{ 
    font-weight: 500;
    font-size: 1.1em;
    text-align:right;
    margin-right: 1%;
    margin-top: 1.5%;
}

.name{
    height: 80px;
    font-weight: 500;
    font-size: 1.1em;
    margin-left: 44%;
 
    
}

.hero-section{
    background:
    linear-gradient(
        rgba(0,0,0,0.45),
          rgba(0,0,0,0.7)
    ),
    url(/jm/static/media/toyko2.32515514.gif) center;
    background-color: black;
    background-size:34%;
    height: 100vh;
    min-height: 100vh;
    box-sizing: border-box;
    position: relative;
}

.marquee{
    position: absolute;
    font-family: "Simplon Norm","Helvetica Neue","Helvetica","Arial",sans-serif;
    text-transform: uppercase;
    font-weight: bold;
    line-height: 1;
    white-space: nowrap;
    font-size: 7.23rem;
    text-shadow: 30px 30px 30px black;
    -webkit-transform: translate(0px,460px);
            transform: translate(0px,460px);
    color: white;
    width: 100%;
    height: 32%;
    z-index: -2;
    
}

.bg{
    position: absolute;
    z-index: -3;
    -webkit-transform: translate(-100px,-280px);
            transform: translate(-100px,-280px);
}

.bg-info{
    position: absolute;
    z-index: -3;
    -webkit-transform: translate(-400px,392px);
            transform: translate(-400px,392px);
    width: 40%;
    height: 44%;
}
.bg-under-info{
    position: absolute;
    z-index: -7;
    -webkit-transform: translate(0px,0);
            transform: translate(0px,0);
    opacity: 0.75;
    height: 100%;
}


