.image1{
    height: 550px;
    -webkit-filter: brightness(80%);
            filter: brightness(80%);
    -webkit-filter: drop-shadow(0 0 0.75rem black);
            filter: drop-shadow(0 0 0.75rem black);
    z-index: 1;
}

.image2,.image3{
    height:550px;
    -webkit-filter: brightness(70%);
            filter: brightness(70%);
    -webkit-filter: drop-shadow(0 0 0.75rem black);
            filter: drop-shadow(0 0 0.75rem black);
    z-index: 1;
    
}

.right-content-stack{
    position: flex;
    -webkit-transform: translate(62%,160px);
            transform: translate(62%,160px);
    width:50%;
    
}
.hero-info{
    font-size: 1.7em;
    width: 65%;
    color:white;
    font-weight: 100;
    font-family: 'Neue Haas Grotesk Display Pro', sans-serif;
}

.images{
    display: flex;
    position: relative;
    width: 100px;
    height:100%;
    margin-top: 5%;
    
    
}

.images div img{
    z-index: 20;
}
.image-stack__item--bottom{
    position: absolute;
    transition: all 0.2s ease-in;
}

.image-stack__item--top{
    position: absolute;
    left: 80%;
    top: 20px;   
    transition: all 0.2s ease-in;
}
.image-stack__item--top2{
    position: absolute;
    top: 50px;
    left: 160%;
    transition: all 0.2s ease-in;

}

.image-stack__item--top3{
    position: absolute;
    top: 75px;
    left: 230%;
    transition: all 0.2s ease-in;

}

.image-stack__item--top4{
    position: absolute;
    top: 100px;
    left: 300%;
    transition: all 0.2s ease-in;

}
.image-stack__item--bottom:hover,.image-stack__item--top:hover,.image-stack__item--top2:hover,.image-stack__item--top3:hover,.image-stack__item--top4:hover{
    -webkit-transform: translate(0,-30px);
            transform: translate(0,-30px);
}

.bottom-hr{
    width: 63%;
    -webkit-transform: translate(80px,440px);
            transform: translate(80px,440px);
}


.fade-in-image{
    -webkit-animation:instant 0.5s;
            animation:instant 0.5s;
}
.fade-in-content{
    -webkit-animation:fadeIn 0.7s;
            animation:fadeIn 0.7s;
}

.fade-in-image2{
    -webkit-animation:instant 0.7s;
            animation:instant 0.7s;
}
.fade-in-image3{
    -webkit-animation: instant 1s;
            animation: instant 1s;
}

.fade-in-image4{
    -webkit-animation: instant 1.1s;
            animation: instant 1.1s;
}

@-webkit-keyframes instant{
    0%{
        opacity: 0;
    }
    99%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes instant{
    0%{
        opacity: 0;
    }
    99%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@-webkit-keyframes fadeIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

@keyframes fadeIn{
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}



